import React, { Component } from "react";
import projectImg from "../../../Assets/pokemon.jpg";
export class Project3 extends Component {
  render() {
    return (
      <section className="project">
        <img className="project__img" src={projectImg} />
        <div className="project__buttons">
        <a className="project-button" href="https://dariuszb94.github.io/pokemon/">Demo</a>

          <a className="project-button" href="https://github.com/Dariuszb94/pokemon">Code</a>
        </div>
        <h3 className="project__title">
        Angular Pokedex
        </h3>
        <div className="project__description">
        List of pokemons fetched from API. You can choose your pokemon from list or search it.
        </div>

        <div className="project__stack">Tech stack: HTML | CSS | Angular | API</div>
      </section>
    );
  }
}
