import React, { Component } from "react";
import projectImg from "../../../Assets/car_dealer.jpg";
export class Project1 extends Component {
  render() {
    return (
      <section className="project">
        <img className="project__img" src={projectImg} />
        <div className="project__buttons">
        <a className="project-button" href="https://dariuszb94.github.io/Lux-cars">Demo</a>
          <a className="project-button" href="https://github.com/Dariuszb94/Lux-cars">Code</a>

        </div>
        <h3 className="project__title">
        Car Dealership
        </h3>
        <div className="project__description">
          Dealership app lets you find your dream car. You can sort, filtrate
          cars, calculate loans for cars. Save favourite offers is also
          possible.
        </div>

        <div className="project__stack">Tech stack: HTML | CSS | React | Redux | IndexedDB | Styled-Components</div>
      </section>
    );
  }
}
