import React, { Component } from "react";
import $ from "jquery";
import html from "../../Assets/html.png";
import js from "../../Assets/js.png";
import css from "../../Assets/css.png";
import react from "../../Assets/react.png";
import angular from "../../Assets/angular.png";
import jquery from "../../Assets/jquery.png";
import vue from "../../Assets/vue.png";
import php from "../../Assets/php.jpg";
import git from "../../Assets/git.png";
import npm from "../../Assets/npm.png";

export class Skills extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.animateLetters = this.animateLetters.bind(this);
  }
  componentDidMount() {
    this.slideTitle();
    this.slideContent();
  }

  slideTitle() {
    var $animation_elements = $(".slide");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);
  }
  hoverCTA() {
    document.getElementById("card--cta").classList.add("card--alt--hover");
  }

  hoverCTAOff() {
    document.getElementById("card--cta").classList.remove("card--alt--hover");
  }
  animateLetters() {
    $(".slide-skills__letter1").css(
      "transform",
      "rotate(920deg) scale(0.5) translateX(4px)"
    );
    $(".slide-skills__letter2").css("transform", "rotate(920deg) scale(0.5)");
    $(".slide-skills__letter3").css("transform", "rotate(1170deg) scale(0.5)");
    $(".slide-skills__letter4").css("transform", "rotate(320deg) scale(0.5)");
    $(".slide-skills__letter5").css("transform", "rotate(-330deg) scale(0.5)");
    $(".slide-skills__letter6").css(
      "transform",
      "rotate(450deg) scale(0.5) translateX(4px)"
    );
  }
  animateLettersLeave() {
    $(".slide-skills__letter").css("transform", "rotate(0) scale(1)");
  }
  animateImg(e) {
    $(e).css("transform", "rotate(1080deg) scale(0.5)");
  }
  animateImgLeave(e) {
    $(e).css("transform", "rotate(0) scale(1)");
  }
  slideContent() {
    var $animation_elements = $(".slide__content");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);
  }
  render() {
    return (
      <section className="skills" id="skills">
        <h2
          className="about__title slide"
          onMouseEnter={() => this.animateLetters()}
          onMouseLeave={() => this.animateLettersLeave()}
        >
          <span className="slide-skills__letter  slide-skills__letter1">S</span>
          <span className="slide-skills__letter slide-skills__letter2">k</span>
          <span className="slide-skills__letter slide-skills__letter3">i</span>
          <span className="slide-skills__letter slide-skills__letter4">l</span>
          <span className="slide-skills__letter slide-skills__letter5">l</span>
          <span className="slide-skills__letter slide-skills__letter6">s</span>
        </h2>
        <div className="about__content slide__content">
          <h3 className="about__content__title skills-header-bg">Frontend</h3>
          <img
            src={html}
            className="about__content__img about__content__img-1 "
            onMouseEnter={() => this.animateImg(".about__content__img-1")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-1")}
          />
          <img
            src={js}
            className="about__content__img about__content__img-2 "
            onMouseEnter={() => this.animateImg(".about__content__img-2")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-2")}
          />
          <img
            src={css}
            className="about__content__img about__content__img-3 "
            onMouseEnter={() => this.animateImg(".about__content__img-3")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-3")}
          />
          <img
            src={react}
            className="about__content__img about__content__img-4 "
            onMouseEnter={() => this.animateImg(".about__content__img-4")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-4")}
          />
          <img
            src={angular}
            className="about__content__img about__content__img-5 "
            onMouseEnter={() => this.animateImg(".about__content__img-5")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-5")}
          />
          <img
            src={jquery}
            className="about__content__img about__content__img-6 "
            onMouseEnter={() => this.animateImg(".about__content__img-6")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-6")}
          />
          <img
            src={vue}
            className="about__content__img about__content__img-8 "
            onMouseEnter={() => this.animateImg(".about__content__img-8")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-8")}
          />
          <h3 className="about__content__title skills-header-bg">Backend</h3>
          <img
            src={php}
            className="about__content__img about__content__img-9 "
            onMouseEnter={() => this.animateImg(".about__content__img-9")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-9")}
          />
          <h3 className="about__content__title skills-header-bg">Tools</h3>
          <img
            src={git}
            className="about__content__img about__content__img-10 "
            onMouseEnter={() => this.animateImg(".about__content__img-10")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-10")}
          />
          <img
            src={npm}
            className="about__content__img about__content__img-11 "
            onMouseEnter={() => this.animateImg(".about__content__img-11")}
            onMouseLeave={() => this.animateImgLeave(".about__content__img-11")}
          />
        </div>
      </section>
    );
  }
}
