import React, { Component } from "react";
import $ from "jquery";

export class ContactFixed extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.rollUp = this.rollUp.bind(this);
  }
  rollUp() {
    $( ".contact" ).css("transform","scaleY(0)");
    
    setTimeout(function(){   $( ".contact-hidden" ).css("transform","scaleX(1)"); }, 300);



  }
  rollDown() {
    // $( ".contact" ).css("transform","scaleY(1)");

  }

  render() {
    return (
      <section className="contact" onMouseEnter={()=>this.rollUp()} onMouseLeave={()=>this.rollDown()}>
        <div className="contact-text">Contact&nbsp;me</div>
      </section>
    );
  }
}
