import React, { Component } from "react";
import projectImg from "../../../Assets/vue-quiz.jpg";
export class Project4 extends Component {
  render() {
    return (
      <section className="project">
        <img className="project__img" src={projectImg} />
        <div className="project__buttons">
          <a className="project-button" href="https://quiz-a885f.web.app/">Demo</a>
        </div>
        <h3 className="project__title">
        Vue Quiz
        </h3>
        <div className="project__description">
        Quiz with limited time for every question - 5 questions. While logging, number is checked with database stored in Firebase (e.g. 111-111-111). Score is stored in Firebase. 
        </div>

        <div className="project__stack">Tech stack: HTML | CSS | Vue | Vuex | Firebase</div>
      </section>
    );
  }
}
