import React, { Component } from "react";
import projectImg from "../../../Assets/Cloth_shop.jpg";
export class Project2 extends Component {
  render() {
    return (
      <section className="project">
        <img className="project__img" src={projectImg} />
        <div className="project__buttons">
        <a className="project-button" href="https://clothshop-1726f.web.app/">Demo</a>

          <a className="project-button" href="https://github.com/Dariuszb94/shop">Code</a>
        </div>
        <h3 className="project__title">
        Vue Cloth Shop
        </h3>
        <div className="project__description">
        You can navigate between cloth sections. Clicking "BUY" adds item to your cart. You can check value of your cart at any moment by clicking on cart icon.
        </div>

        <div className="project__stack">Tech stack: HTML | CSS | Vue | Vuex</div>
      </section>
    );
  }
}
