import React, { Component } from "react";
import { Project1 } from "./ProjectsComponents/Project1";
import { Project2 } from "./ProjectsComponents/Project2";
import { Project3 } from "./ProjectsComponents/Project3";
import { Project4 } from "./ProjectsComponents/Project4";


import $ from "jquery";

export class Projects extends Component {
  componentDidMount() {
    this.slideTitle();
    this.slideContent();
  }

  slideTitle() {
    var $animation_elements = $(".slide");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);
  }
  animateLetters() {
    $(".project__letter1").css("transform", "translateY(20px) scale(0.9)");
    $(".project__letter2").css("transform", "translateY(-10px) scale(0.7)");
    $(".project__letter3").css("transform", "translateY(10px) scale(0.8)");
    $(".project__letter4").css("transform", "translateY(-15px) scale(0.9)");
    $(".project__letter5").css("transform", "translateY(-20px) scale(0.7)");
    $(".project__letter6").css("transform", "translateY(5px) scale(0.8)");
    $(".project__letter7").css("transform", "translateY(-10px) scale(0.7)");
    $(".project__letter8").css("transform", "translateY(10px) scale(0.9)");

  }
  animateLettersLeave() {
    $(".slide__letter").css("transform", "rotate(0) scale(1)");
  }
  slideContent() {
    var $animation_elements = $(".slide__content");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);
  }
  render() {
    return (
      <section className="about" id="projects">
        <h2
          className="about__title slide"
          onMouseEnter={() => this.animateLetters()}
          onMouseLeave={() => this.animateLettersLeave()}
        >
          <span className="slide__letter project__letter1">P</span>
          <span className="slide__letter project__letter2">r</span>
          <span className="slide__letter project__letter3">o</span>
          <span className="slide__letter project__letter4">j</span>
          <span className="slide__letter project__letter5">e</span>
          <span className="slide__letter project__letter6">c</span>
          <span className="slide__letter project__letter7">t</span>
          <span className="slide__letter project__letter8">s</span>
        </h2>
        <div className="about__content slide__content">
          <Project1 />
          <Project2 />
          <Project4 />
          <Project3 />
        </div>
      </section>
    );
  }
}
